<template>
  <div class="">
    <GoBack />
    <v-row class="text-center">
      <v-col cols="12">
        <h1>{{ destination.name }}</h1>
      </v-col>
      <v-col cols="auto">
        <v-img :src="require(`@/assets/${destination.image}`)" rounded></v-img>
      </v-col>
      <v-col class="purple--text text-justify">
        <p class="pr-3">{{ destination.description }}</p>
      </v-col>
    </v-row>

    <v-row class="text-center mt-5">
      <v-col cols="12" id="experience">
        <h1>Top experiences in {{ destination.name }}</h1>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="3"
        v-for="experience in destination.experiences"
        :key="experience.slug"
      >
        <router-link
          :to="{
            name: 'experienceDetails',
            params: { experienceSlug: experience.slug },
            hash: '#experience'
          }"
        >
          <v-card>
            <v-img
              :src="require(`@/assets/${experience.image}`)"
              class="white--text align-center pl-4"
              gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
              height="200px"
            >
              <v-card-title>
                {{ experience.name }}
              </v-card-title>
            </v-img>
          </v-card>
        </router-link>
      </v-col>
    </v-row>
    <router-view></router-view>
  </div>
</template>

<script>
// @ is an alias to /src
import store from "@/store.js";
import GoBack from "@/components/GoBack";

export default {
  name: "DestinationDetails",
  components: {
    GoBack
  },
  data: () => ({
    destinations: store.destinations
  }),
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  computed: {
    destination() {
      return this.destinations.find(
        destination => destination.slug === this.slug
      );
    }
  }
};
</script>
